import Vue from 'vue'
import Vuex from 'vuex'

import user from './modules/user'
import nodeName from './modules/nodeName'


Vue.use(Vuex)


export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  modules: {
    user,
    nodeName,
  },
})



